import { Component, h, Element, State, Host } from '@stencil/core';
import { ICellRendererComp, ICellRendererParams } from 'ag-grid-community';

export interface IMenuAction {
    title: string;
    onTrigger?: (params: any) => void;
    getDisabled?: (data: any) => boolean;
    disabledTooltip?: string;
}

export interface IMenuCellParams {
    icon?: string;
    actions: (IMenuAction | string)[];
}

/**
 * @category Table Cell
 * @description A cell component for displaying a menu.
 */

@Component({
    tag: 'atui-menu-cell',
    shadow: false,
})
export class AtuiMenuCellComponent implements ICellRendererComp {
    @Element() el;
    @State() params: ICellRendererParams & IMenuCellParams;

    init(params: ICellRendererParams & IMenuCellParams): void {
        this.params = params;
    }

    getGui(): HTMLElement {
        return this.el;
    }

    refresh(_): boolean {
        return false;
    }

    render() {
        return (
            <Host class="flex h-full items-center gap-4">
                {this.params.value && <span>{this.params.value}</span>}
                <atui-menu>
                    <atui-button
                        type="secondaryText"
                        icon={this.params.icon ?? 'more_vert'}
                        slot="menu-trigger"
                    />
                    <div
                        slot="menu-content"
                        class="flex max-h-120 w-full flex-col overflow-auto"
                    >
                        {this.params.actions &&
                            this.params.actions.map((action) =>
                                typeof action === 'object' ? (
                                    <atui-tooltip
                                        is_visible={
                                            action.getDisabled
                                                ? action.getDisabled(
                                                      this.params.data,
                                                  )
                                                : false
                                        }
                                    >
                                        sssssssss
                                        <atui-button
                                            class="w-full *:justify-start"
                                            type="secondaryText"
                                            label={action.title}
                                            slot="tooltip-trigger"
                                            disabled={
                                                action.getDisabled
                                                    ? action.getDisabled(
                                                          this.params.data,
                                                      )
                                                    : false
                                            }
                                            onClick={(params) => {
                                                if (
                                                    action.onTrigger !==
                                                    undefined
                                                )
                                                    action.onTrigger(params);
                                            }}
                                        ></atui-button>
                                        <span slot="tooltip-content">
                                            {action.disabledTooltip ?? ''}
                                        </span>
                                    </atui-tooltip>
                                ) : (
                                    <span class="py-4">sssssssss{action}</span>
                                ),
                            )}
                    </div>
                </atui-menu>
            </Host>
        );
    }
}
